p {
    font-size: 18px;
}

.input {
    width: 200px;
    height: 25px;
    border-radius: 15px;
    border: 2px solid rgb(54, 66, 238);
    background-color: rgb(255, 255, 255);
    padding: 5px 15px;
    font-size: 18px;
    outline: rgb(54, 66, 238);
    margin-bottom: 25px;
}

input:focus {
    background-color: #f1f1f1;

}

form {
    position: relative;
}

.nameError {
    font-size: 12px;
    color: red;
    position: absolute;
    top: 75px;
    left: 50%;
    transform: translate(-50%);
}

.phoneError {
    font-size: 12px;
    color: red;
    position: absolute;
    top: 170px;
    left: 50%;
    transform: translate(-50%);

}


.errorField {
    border-color: red;
}

form {
    display: flex;
    flex-direction: column;
    width: 300px;
    margin: 0 auto;
    padding: 20px;
    border: 2px solid rgb(54, 66, 238);
    border-radius: 40px;
    padding: 40px;
}

label {
    margin-top: 10px;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"] {

    padding: 5px;
    border-radius: 5px;
    border: 1px solid rgb(54, 66, 238);
    width: 145px;
}

button {
    background-color: rgb(54, 66, 238);
    color: #fff;
    border: none;
    border-radius: 8px;
    padding: 5px 15px;
    font-size: 18px;
    cursor: pointer;
    margin-top: 10px;
    border-radius: 5px;
    transition: scale, linear, 300ms;
}

button:hover {
    box-shadow: 0px 0px 100px 100px rgba(34, 60, 80, 0.7) inset;
    scale: 1.1;
}

button:active {
    box-shadow: none;
}


h2 {
    text-align: center;
    margin: 0;
    margin-bottom: 15px;
}

.labelBox {
    display: flex;
    justify-content: space-between;
    align-items: center;
    justify-self: center;
    margin-bottom: 15px;
    position: relative;
}

.nameError {
    position: absolute;
    top: 100%;
    right: 0;
    color: red;
    font-size: 12px;


}